<template>

    <div class="container">
        <div class="shopping-cart">
            <div class="shopping-cart-header">
                <i class="fa fa-shopping-cart cart-icon"></i><span class="badge">{{totalQty}}</span>
                <div class="shopping-cart-total">
                    <span class="lighter-text">Total:</span>
                    <span class="main-color-text">${{totalPrice}}</span>
                </div>
            </div> <!--end shopping-cart-header -->

            <ul class="shopping-cart-items">
                <li class="clearfix" style="list-style: none;"  v-for="product in cart" :key="product.id">
                    <img style="width: 80px" v-bind:src="product.img" alt="item1"/>
                    <span class="item-name">{{product.title}}</span>
                    <span class="item-price">${{product.price}}</span>
                    <span class="item-quantity">Quantity: {{product.qty}}</span>
                    <br>
                    <button @click="addQty(product.id)">+</button>
                    <button @click="reduceQty(product.id)">-</button>
                    <button @click="removeItem(product.id)">x</button>
                </li>
            </ul>

            <a href="#" :disabled="!isProcessing" @click="placeOrder" v-if="!isProcessing" class="button"
               :style="totalQty > 0
            ? {}
            : {'background-color': 'gray', 'cursor': 'not-allowed'}" >
                Зказать
            </a>
            <div v-else>
                <a href="#" class="button">
                    Loading...
                </a>
            </div>
        </div> <!--end shopping-cart -->
    </div> <!--end container -->
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    export default {
        name: "Cart",
        data() {
            return {
                isProcessing: false,
                orderPlaced: false
            }
        },
        computed: {
            ...mapGetters(["cart"]),
            totalPrice()
            {
                return this.cart.reduce((a,b) => a + b.qty * b.price, 0)
            },
            totalQty()
            {
                return this.cart.reduce((a,b) => a + b.qty, 0)
            },
        },
        methods: {
            ...mapActions(["addQty", "reduceQty", "removeItem", "totalPrice", "emptyCart"]),

            placeOrder(){
                this.isProcessing = true;
                setTimeout(() => {
                    this.orderPlaced = true;
                    this.isProcessing = false;
                    this.emptyCart();
                }, 1000)
                return this.alert = !this.alert;
            }
        }
    }
</script>

<style scoped>
    @import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
    @import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css);
    *, *:before, *:after {
        box-sizing: border-box;
    }
    body {
        font: 14px/22px "Lato", Arial, sans-serif;
        background: #6394f8;
    }
    .lighter-text {
        color: #abb0be;
    }
    .main-color-text {
        color: #6394f8;
    }
    nav {
        padding: 20px 0 40px 0;
        background: #f8f8f8;
        font-size: 16px;
    }
    nav .navbar-left {
        float: left;
    }
    nav .navbar-right {
        float: right;
    }
    nav ul li {
        display: inline;
        padding-left: 20px;
    }
    nav ul li a {
        color: #777;
        text-decoration: none;
    }
    nav ul li a:hover {
        color: black;
    }
    .container {
        margin: auto;
        width: 80%;
    }
    .badge {
        background-color: #6394f8;
        border-radius: 10px;
        color: white;
        display: inline-block;
        font-size: 12px;
        line-height: 1;
        padding: 3px 7px;
        text-align: center;
        vertical-align: middle;
        white-space: nowrap;
    }
    .shopping-cart {
        margin: 20px 0;
        float: right;
        background: white;
        width: 320px;
        position: relative;
        border-radius: 3px;
        padding: 20px;
    }
    .shopping-cart .shopping-cart-header {
        border-bottom: 1px solid #e8e8e8;
        padding-bottom: 15px;
    }
    .shopping-cart .shopping-cart-header .shopping-cart-total {
        float: right;
    }
    .shopping-cart .shopping-cart-items {
        padding-top: 20px;
    }
    .shopping-cart .shopping-cart-items li {
        margin-bottom: 18px;
    }
    .shopping-cart .shopping-cart-items img {
        float: left;
        margin-right: 12px;
    }
    .shopping-cart .shopping-cart-items .item-name {
        display: block;
        padding-top: 10px;
        font-size: 16px;
    }
    .shopping-cart .shopping-cart-items .item-price {
        color: #6394f8;
        margin-right: 8px;
    }
    .shopping-cart .shopping-cart-items .item-quantity {
        color: #abb0be;
    }
    .shopping-cart:after {
        bottom: 100%;
        left: 89%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-bottom-color: white;
        border-width: 8px;
        margin-left: -8px;
    }
    .cart-icon {
        color: #515783;
        font-size: 24px;
        margin-right: 7px;
        float: left;
    }
    .button {
        background: #6394f8;
        color: white;
        text-align: center;
        padding: 12px;
        text-decoration: none;
        display: block;
        border-radius: 3px;
        font-size: 16px;
        margin: 25px 0 15px 0;
    }
    .button:hover {
        background: #729ef9;
    }
    .clearfix:after {
        content: "";
        display: table;
        clear: both;
    }

</style>